import React, { useState, useEffect } from 'react'
import { useSelector, useDispatch } from 'react-redux'

import {
  setJson,
  selectJson,
  selectDocumentStatus,
  setDocumentStatus
} from '../features/json/jsonSlice'
import {
  callSection
} from '../features/tabulation/tabulationSlice'
import {
  setErrorMsg,
  clearErrorMsg
} from '../features/display/displaySlice'

import { normalizeJson, getElementFromElementId, getDocumentExport } from '../app/utilities'

function ApiInterface() {

  const json = useSelector(selectJson)
  const dispatch = useDispatch()
  const documentStatus = useSelector(selectDocumentStatus)

  const apiURL = 'https://api-uat.extrack.com/'

  const [token, setToken] = useState({})
  const [user, setUser] = useState({})
  const [projects, setProjects] = useState([])
  const [templates, setTemplates] = useState([])
  const [documents, setDocuments] = useState([])
  const [selectedProjectId, setSelectedProjectID] = useState({})
  const [selectedTemplateId, setSelectedTemplateID] = useState({})
  const [selectedTemplate, setSelectedTemplate] = useState({})
  const [selectedDocument, setSelectedDocument] = useState({})

  const [isFetchingTemplates, setIsFetchingTemplates] = useState(false)
  const [isFetchingDocuments, setIsFetchingDocuments] = useState()
  const [loadingAnim, setLoadingAnim] = useState('')

  // const [errorMsg, setErrorMsg] = useState('')

  const username = 'red.marble'
  const password = 'Welcome01!'
  const member = 'downer'


  // useEffect(() => {
  //   console.log('---> user set: ', user)
  // }, [user])

  useEffect(() => {
    if (documentStatus === 'tosend') {
      // console.log(`---> Sending document: ${JSON.stringify(getDocumentExport())} for project: ${selectedProjectId} and template: ${selectedTemplateId}`)

      let metadata = JSON.stringify([getDocumentExport()])
      console.log(`----> metadata`, metadata)


      const headers = new Headers()
      // headers.append("Content-Type", "application/json")
      headers.append("Authorization", `Bearer ${token.access_token}`)

      // JSON.stringify triple metadata backslashes
      const documentBody = JSON.stringify({
        "ID": 0,
        "ProjectID": selectedProjectId,
        "Description": "",
        "Title": "Test-NewDoc",
        "DocumentTypeId": 1088,
        "DocumentFormTemplateID": selectedTemplateId,
        "UserRevisionNumber": "0",
        "UserRevisionDate": "2020-09-25",
        "MetaData": metadata,
        "OpenClosedStatus": "O",
        "CreatedByContactID": 1059,
        "CreatedByCompanyID": 1009
      })

      const documentBodyString = JSON.stringify(documentBody)

      let formdata = new FormData()
      formdata.append("Document", documentBody)


      console.log(`--->: sendind with body ------>  :   `, documentBodyString)


      const requestOptions = {
        method: 'POST',
        headers: headers,
        body: formdata
        // body: JSON.stringify(requestBody)
        // body: JSON.stringify({
        //   "ID": 0,
        //   "ProjectID": 1249,
        //   "Description": "",
        //   "Title": "Test-NewDoc",
        //   "DocumentTypeId": 1088,
        //   "DocumentFormTemplateID": 25,
        //   "UserRevisionNumber": "0",
        //   "UserRevisionDate": "2020-09-10",
        //   "MetaData": "[{\"Elements\":[{\"Type\":\"Trilean\",\"Value\":\"N\",\"Id\":\"S032\",\"CommentValue\":\"TrileanCommentvaluefromdocument\"}]}]",
        //   "OpenClosedStatus": "O",
        //   "CreatedByContactID": 1059,
        //   "CreatedByCompanyID": 1009
        // })
      }

      fetch(`${apiURL}V1.0/Project/${selectedProjectId}/Document/Template/${selectedTemplateId}/Create`, requestOptions)
        .then(res => res.text())
        .then(result => console.log('response: ', result))
        .catch(error => console.log('error', error))




    }
    // dispatch(setDocumentStatus('sent'))
  }, [documentStatus])

  useEffect(() => {
    // console.log('---> template set: ', selectedTemplate)
    if (selectedTemplate.Config !== undefined) {
      const configJson = selectedTemplate.Config
      // console.log(`------------------>: configJson: `, configJson)
      // const configJsonNormalized = normalizeJson(configJson)

      localStorage.setItem('initialJson', JSON.stringify(normalizeJson(JSON.parse(configJson))))
      dispatch(setJson(normalizeJson(JSON.parse(configJson))))
      // console.log('-=--> ', normalizeJson(configJson))

    }
    // dispatch(setJson(decodeURI()))
  }, [selectedTemplate])

  // useEffect(() => {
  //   if (selectedDocument.MetaData !== undefined) {
  //     const metaDataJson = selectedDocument.MetaData
  //     console.log('-=--> ', JSON.parse(metaDataJson))
  //   }
  //   dispatch(setJson(decodeURI()))
  // }, [selectedDocument])


  const getTokenHandler = () => {
    const headers = new Headers()
    headers.append("Content-Type", "application/x-www-form-urlencoded")

    let urlencoded = new URLSearchParams()
    urlencoded.append("username", username)
    urlencoded.append("password", password)
    urlencoded.append("MemberCode", member)
    urlencoded.append("grant_type", "password")
    urlencoded.append("timezone", "Australia/Sydney")
    // console.log('params: ', urlencoded.toString())

    fetch(`${apiURL}oAuth/Token`, {
      method: 'POST',
      headers: headers,
      body: urlencoded
    })
      .then(res => res.text())
      .then(result => {
        const receivedToken = JSON.parse(result)
        // console.log('receivedToken: ', receivedToken)
        getUser(receivedToken.access_token)
        setToken(receivedToken)
        localStorage.setItem('dynFormToken', JSON.stringify(receivedToken))
      })
      .catch(error => console.log('error', error))
  }

  const getUser = (pToken) => {
    const userHeaders = new Headers()
    userHeaders.append("Authorization", `Bearer ${pToken}`)

    const requestOptions = {
      method: 'GET',
      headers: userHeaders
    }

    fetch(`${apiURL}V1.0/User`, requestOptions)
      .then(response => response.text())
      .then(result => setUser(JSON.parse(result)))
      .catch(error => console.log('error', error))
  }

  function getProjectsHandler() {
    const projectsHeaders = new Headers()
    projectsHeaders.append("Authorization", `Bearer ${token.access_token}`)
    projectsHeaders.append("Content-Type", "application/x-www-form-urlencoded")

    const requestOptions = {
      method: 'GET',
      headers: projectsHeaders,
      redirect: 'follow'
    }

    fetch(`${apiURL}V1.0/Project`, requestOptions)
      .then(response => response.text())
      .then(result => {
        // console.log('projects: ', JSON.parse(result))
        setProjects(JSON.parse(result))
      })
      .catch(error => console.log('error', error))
  }

  function getTemplates(pProjectID) {
    setIsFetchingTemplates(true)

    const templatesHeaders = new Headers()
    templatesHeaders.append("Authorization", `Bearer ${token.access_token}`)
    templatesHeaders.append("Content-Type", "application/x-www-form-urlencoded")

    const requestOptions = {
      method: 'GET',
      headers: templatesHeaders,
      redirect: 'follow'
    }

    fetch(`${apiURL}V1.0/Project/${pProjectID}/Document/Form/Templates`, requestOptions)
      .then(response => response.text())
      .then(result => {
        // console.log('templates: ', JSON.parse(result))
        setTemplates(JSON.parse(result))
        setIsFetchingTemplates(false)
      })
      .catch(error => console.log('error', error))
  }

  function getTemplateById(pTemplateID) {
    const currentTemplate = templates.filter((template) => template.Id === pTemplateID)
    // console.log(`--->: template: `, JSON.parse(currentTemplate[0].Config))
    return JSON.parse(currentTemplate[0].Config)
  }

  function getDocuments(pProjectID) {
    setIsFetchingDocuments(true)

    const documentsHeaders = new Headers()
    documentsHeaders.append("Authorization", `Bearer ${token.access_token}`)
    documentsHeaders.append("Content-Type", "application/x-www-form-urlencoded")

    const requestOptions = {
      method: 'GET',
      headers: documentsHeaders,
      redirect: 'follow'
    }

    fetch(`${apiURL}V1.0/Project/${pProjectID}/Documents?offset=0&limit=40&orderBy=Id desc`, requestOptions)
      .then(response => response.text())
      .then(result => {
        // console.log(' ====> doucments: ', JSON.parse(result))
        setDocuments(JSON.parse(result))
        setIsFetchingDocuments(false)
      })
      .catch(error => console.log('error', error))
  }

  function selectProjectHandler(pEvent) {
    if (pEvent.target.selectedIndex !== 0) {
      const projectID = projects[pEvent.target.selectedIndex - 1].Id
      // console.log(`--->: selectProjectHandler -> projectID`, projectID)

      setSelectedProjectID(projectID)
      getTemplates(projectID)
      getDocuments(projectID)
    }
    else setTemplates([])

  }

  function selectTemplateHandler(pEvent) {
    document.getElementById('documents').selectedIndex = 0
    dispatch(clearErrorMsg())
    // console.log('---> ', templates[pEvent.target.selectedIndex - 1])
    // normalizeJson(templates[pEvent.target.selectedIndex])
    // const templateJson = templates[pEvent.target.selectedIndex].Config
    if (pEvent.target.selectedIndex !== 0) {
      dispatch(callSection(0))
      setSelectedTemplate(templates[pEvent.target.selectedIndex - 1])
      setSelectedTemplateID(templates[pEvent.target.selectedIndex - 1].Id)
    }
  }

  function selectDocumentHandler(pEvent) {
    document.getElementById('templates').selectedIndex = 0
    dispatch(clearErrorMsg())
    // console.log('---> Document: ', documents[pEvent.target.selectedIndex - 1])
    // normalizeJson(templates[pEvent.target.selectedIndex])
    // const templateJson = templates[pEvent.target.selectedIndex].Config
    if (pEvent.target.selectedIndex !== 0) {
      // dispatch(callSection(0))
      const document = documents[pEvent.target.selectedIndex - 1]
      setSelectedDocument(document)
      // setSelectedTemplate(templates[pEvent.target.selectedIndex - 1])

      const template = getTemplateById(document.DocumentFormTemplateId)
      aggregateJson(template, JSON.parse(document.MetaData)[0])
      // console.log('--__-> Need template: ', document.DocumentFormTemplateId)

    }
    // setSelectedProjectID(projects[pEvent.target.selectedIndex - 1].Id)
  }

  function aggregateJson(pTemplate, pDocument) {
    const templateToUpdate = normalizeJson(pTemplate)
    // console.log(pTemplate, templateToUpdate, pDocument)
    console.log('templateToUpdate :', templateToUpdate, ' on document: ', pDocument)

    pDocument.Elements.forEach(item => {
      const itemPath = getElementFromElementId(pTemplate, item.Id)
      console.log(`--->: aggregateJson -> itemPath`, itemPath)


      if (itemPath.length > 1) dispatch(setErrorMsg(`Multiple ID '${item.Id}' found in the template`))
      if (itemPath.length === 0) return
      const firstFoundedPath = itemPath[0]
      console.log('item value: ', item, ' to update: ', firstFoundedPath)

      switch (itemPath[0].length) {
        case 2:
          const templateValues = templateToUpdate.Sections[firstFoundedPath[0]].Elements[firstFoundedPath[1]]
          templateToUpdate.Sections[firstFoundedPath[0]].Elements[firstFoundedPath[1]] = { ...templateValues, ...item }
          // console.log('---> valuesToInsert', templateValues)
          break
        case 3:
          console.log('---> two levels')
          break
        case 4:
          console.log('---> three levels')
          break
        default:
          break
      }
    })
    console.log('Json updated: ', templateToUpdate)
    dispatch(setJson(templateToUpdate))
  }



  const projectsList = projects.map((project, index) => <option value={project.Id} key={index}>{project.Abbr}</option>)
  const templatesList = templates.map((template, index) => <option value={template.Id} key={index}>{template.Title1}</option>)
  const documentsList = documents.map((document, index) => <option value={document.Id} key={index}>{document.DocumentNumber}</option>)


  useEffect(() => {
    let trail = ''
    setInterval(() => {
      trail = (trail.length > 3) ? '' : trail
      trail += '.'
      setLoadingAnim(trail)
    }
      , 200)
  }, [])



  const fetchingTemplates = isFetchingTemplates ? <div className="spinner">Fetching Templates{loadingAnim}</div> : ''
  const fetchingDocuments = isFetchingDocuments ? <div className="spinner">Fetching Documents{loadingAnim}</div> : ''
  return (
    <div className="bloc">
      <button className={`btn btn-ternary ${user.NameFirst ? 'hidden' : ''}`} onClick={getTokenHandler}>LOGIN</button>
      <button className={`btn btn-ternary ${user.NameFirst === undefined || projectsList.length > 0 ? 'hidden' : ''}`} onClick={getProjectsHandler}>GET PROJECTS</button>

      <div className={`bloc-menu ${projects.length > 0 ? '' : 'hidden'}`}>
        <label htmlFor="projects">SELECT Project</label>
        <select name="projects" id="projects" onChange={selectProjectHandler}>
          <option value="-1">-----</option>
          {projectsList}</select>
      </div>

      {fetchingTemplates}
      <div className={`${templates.length > 0 ? '' : 'hidden'} bloc-menu bloc-menu__separator`}>
        <label htmlFor="templates">SELECT Template</label>
        <select name="templates" id="templates" onChange={selectTemplateHandler}>
          <option value="">-----</option>
          {templatesList}</select>
      </div>

      <div className='bloc-spacer'>
        <span className={`${templates.length > 0 ? '' : 'hidden'}`}>
          OR
        </span>
      </div>


      {fetchingDocuments}
      <div className={`${documents.length > 0 ? '' : 'hidden'} bloc-menu`}>
        <label htmlFor="documents">SELECT Document</label>
        <select name="documents" id="documents" onChange={selectDocumentHandler}>
          <option value="">-----</option>
          {documentsList}</select>
      </div>

      {/* <div className={`${errorMsg.length > 0 ? '' : 'hidden'} error-msg`}>
        {errorMsg}
      </div> */}
    </div>
  )
}

export default ApiInterface